  width: 100vw;
  z-index: 2;
  background: rgba(0, 0, 0, .8);
  transition: opacity .25s, visibility .25s;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.backdrop__modal.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal__footer {
  min-height: 100%;
  justify-content: center;
  align-items: center;
  padding: 30px 10px;
  display: flex;
}

.modal__content {
