.profile-card .img {
  width: 100%;
  height: 100%;
  z-index: 99;
  filter: grayscale();
  transition: all .6s;
  position: relative;
}

.profile-card:hover .img, .profile-card:focus .img {
  filter: grayscale(0%);
  transform: translateY(-60px);
}

.img img {
  width: 100%;
  border-radius: 50%;
