.popup {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .8);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.popup__body {
  max-width: 340px;
  background: #fff;
  border-radius: 4px;
  padding: 20px;
  position: absolute;
  top: 50%;
